import React from "react";
import styled from "styled-components";
import { images } from "../data/images";
const NotesContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding-top: 15px;
  height: auto;
  user-select: none;
`;
const NoteName = styled.p`
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  @media screen and (max-width: 700px) {
      opacity: 1;
  }
`;
const Note = styled.div`
  height: 200px;
  flex-grow: 1;
  margin: 2px;
  background: #fff;
  cursor: pointer;
  position: relative;
  &:hover {
    opacity: 0.8;
  }
  &:hover ${NoteName} {
    opacity: 1;
  }
`;
const NoteImage = styled.img`
  height: 200px;
  object-fit: cover;
  max-width: 100%;
  min-width: 100%;
  vertical-align: bottom;
`;
const NotesView = () => {
  return (
    <NotesContainer>
      {images &&
        images.map((d) => (
          <Note>
            <NoteImage src={d && d.image} />
            <NoteName>{d && d.name}</NoteName>
          </Note>
        ))}
    </NotesContainer>
  );
};
export default NotesView;
